.grid {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  max-width: 100%;
  width: 100%;
}

.box {
  background-color: #ffffff;
  border-radius: 5px;    
  padding: 14px 20px 18px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: calc(25% - 1rem);
  border: 1px solid transparent;
  text-decoration: none;
  transition: all .2s;
}

.box:hover {
  background-color: #ecf8ff;
  border-color: var(--accent-color);
}

.box[data-overdue="true"] {
  border: 1px solid red;
  background-color: rgb(255, 236, 236);
}

.box[data-overdue="true"]:hover {
  background-color: rgb(255, 223, 223);
}

.box[data-overdue="true"] .title {
  color: red;
}

.box[data-overdue="true"] .deadline {
  color: red;
}

.box[data-overdue="true"] button:first-of-type {    
  background-color: red;
}

.box[data-overdue="true"] button:last-of-type {    
  color: red;
  border-color: red;
}

.box[data-finished="true"] {
  border: 1px solid green;
  background-color: rgb(236, 255, 241);
}

.box[data-finished="true"]:hover {
  background-color: rgb(210, 252, 221);
}

.box[data-finished="true"] .title {
  color: #ccc;
}

.box[data-finished="true"] button:first-of-type {    
  background-color: #35e454;
}

.box[data-finished="true"] button:last-of-type {    
  color: #35e454;
  border-color: #35e454;
}

.box[data-finished="true"] .deadline {
  color: #7f8c8d;
}

.title {
  color: var(--primary-color);
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: -6px;
}

.deadline {
  color: #7f8c8d;
  border-bottom: 1px solid #ecf0f1;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
}

.usersWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.responsibleUser {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #000;
}

.users {
  display: flex;
}

.users img,
.users span {
  margin-left: -10px;
  color: #000;
}

.responsibleUser img,
.users img,
.users span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  object-fit: cover;
  background-color: #ffffff;
  border: 1px solid #ccc;
}

.buttonWrapper {
  display: flex;
  gap: 10px;
}

.buttonWrapper button {
  width: 50%;
  padding: 10px;
}

.buttonWrapper button:last-of-type {
  background: none;
  color: var(--accent-color);
  border: 1px solid var(--accent-color);
}