form {
  max-width: 400px;
  margin: 2rem auto;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  border-radius: 8px;
}

label {
  display: block; /* ensures that each label takes the full width */
  margin-bottom: 1.5rem; /* spacing between labels */
}

label > span, .label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold; /* makes the label text bold */
}

input {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

input:hover {
  border-color: #999;
}

input:focus,
textarea:focus,
select:focus {
  border-color: #0079bf;
  outline: none;
}

input[type="file"] {
  padding: 0.5rem;
}

.image-selector {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.image-selector img {
  max-height: 50px;
  border-radius: 4px;
  object-fit: cover;
  flex-shrink: 0;
}

.image-selector input[type="file"] {
  flex-grow: 1;
}

button {
  width: 100%;
  padding: 0.8rem 1rem;
  background-color: var(--accent-color);
  color: #ffffff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #005a8e;
}

button:active {
  background-color: #00436a;
}

select {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  margin-top: 0.5rem; /* Add spacing between elements */
}

textarea {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  margin-top: 0.5rem; /* Add spacing between elements */
}

.dropdown-list {
  z-index: 1000;
  position: absolute;
  width: 100%;
  min-width: 340px;
  background: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  max-height: 180px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.dropdown-list-item {
  display: flex;
  align-items: center;
  gap: 6px;
  width: 100%;
  padding: 10px;
}

.dropdown-list-item:hover {
  background-color: rgb(235, 235, 235);
}

.dropdown-list-item--highlight {
  background-color: #e6f6ff;
  font-weight: 600;
}

.dropdown-list-item--highlight:hover {
  background-color: #c9ebff;
}

.removeAttrBtn {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #eeeeee;
  width: 34px;
  height: 34px;
  border-radius: 4px;
  margin-left: 10px;
  transition: background-color .2s;
}

.removeAttrBtn:hover {
  background-color: #d6d6d6;
}

/* Fehlermeldungen */
.error-msg {
  margin-top: 1rem;
  color: #d32f2f;  /* Ein rötlicher Farbton für Fehlermeldungen */
  background-color: #ffebee;  /* Ein sehr leicht rötlicher Hintergrund */
  padding: 0.8rem 1rem;
  border-radius: 5px;
}

/* Erfolgsmeldungen */
.success-msg {
  margin-top: 1rem;
  color: #2e7d32; /* Ein grünlicher Farbton für Erfolgsmeldungen */
  background-color: #e8f5e9; /* Ein sehr leicht grünlicher Hintergrund */
  padding: 0.8rem 1rem;
  border-radius: 5px;
}