.adminPanel {
  background-color: var(--primary-color);
  padding: 2rem 0;
  width: 250px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

.header {
  color: #ecf0f1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

.userImg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.header h2 {
  margin: 0;
  font-size: 1.2rem;
}

.links {
  display: flex;
  flex-direction: column;
}

.link {
  color: #ecf0f1;
  text-decoration: none;
  padding: 14px 20px;
  transition: background-color 0.2s;
  cursor: pointer;
}

.link:hover {
  background-color: #3498db;
}

.link[class*="active"] {
  background-color: #3498db;
}

.accordionContainer {
  border-top: 1px solid #435970;
  border-bottom: 1px solid #435970;
  margin: 1rem 0;
}

.accordionTitle {
  color: #ecf0f1;
  cursor: pointer;
  padding: 0.8rem 1.2rem;
  transition: background-color 0.3s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.arrow {
  border: solid #ecf0f1;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(45deg);
  transition: transform 0.3s ease;
}

.accordionTitle.open .arrow {
  transform: rotate(-135deg);
}

.accordionTitle:hover {
  background-color: #3498db;
}

.accordionContent {
  display: flex;
  flex-direction: column;
  padding-bottom: 12px;
  padding-left: 12px;
}