.form {
  max-width: initial;
  margin: 2rem 36px;
}

.usersSection {
  margin-top: 1.5rem; /* Add spacing between elements */
}

.usersForm,
.typesForm {
  position: relative;
  margin-bottom: 1.5rem;
}

.usersSelection {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 14px;
}

.userWrapper {
  position: relative;
}

.userWrapper[data-row="true"] {
  display: flex;
  align-items: center;
  gap: 6px;
}

.userIcon {
  width: 49px;
  height: 49px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

[class*="project-detail"] .userIcon {
  width: 36px;
  height: 36px;
}

.userIcon {
  pointer-events: none;
  border: 1px solid #bbbbbb;
}

.removeUserBtn {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: -6px;
  border-radius: 4px;
  width: 20px;
  height: 20px;
  font-size: 12px;
  background-color: #eeeeee;
  transition: background-color .2s;
}

.removeUserBtn:hover {
  background-color: #d6d6d6;
}

.dropdownImg {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

[class*="project-detail"] .dropdownImg {
  width: 36px;
  height: 36px;
}

.dropdownName,
.dropdownEmail {
  font-weight: 400;
  margin-bottom: 0;
}

.dropdownEmail {
  margin-left: auto;
}

[class*="project-detail"] .dropdownEmail {
  margin-left: auto;
  width: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.selectedType {
  display: flex;
  align-items: center;
  gap: 6px;
  height: 49px;
}