.projectWrapper {
  display: flex;
  align-items: flex-start;
  gap: 18px;
  padding: 2rem;
}

.projectBox {
  width: 60%;
}

.commentsBox {
  width: 40%;
}

.projectBox,
.commentsBox {
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.projectDetails,
.commentsBox {
  padding: 18px;
}

.projectInput[data-row="true"] {
  display: flex;
  white-space: nowrap;
  align-items: center;
  margin-bottom: 4px;
}

.projectInput input {
  border-color: transparent;
  padding: 6px;
  width: auto;
}

.projectInput input:hover {
  border-color: #ccc;
}

.projectInput [class="projectLabel"] {
  margin-left: 6px;
  margin-bottom: 0;
  margin-top: 14px;
}

.projectInput > span {
  font-weight: 600;
}

.projectInput[data-row="true"] [class="projectLabel"] {
  margin-bottom: 0;
  margin-top: 0;
  width: 120px;
  min-width: 120px;
}

.projectInput[data-row="true"] > div {
  margin-bottom: 0;
}

.projectInput[data-row="true"] > div > div:not([class*="dropdown"]) {
  height: 36px;
}

.projectInput textarea {
  max-width: calc(100% - 6px);
  min-width: calc(100% - 6px);
  min-height: 300px;
}

.projectInput input[type="date"] {
  margin-left: -6px;
}

.projectTitle {
  width: 100% !important;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 12px;
}

.projectDesc {
  border: 1px solid #ccc !important;
  margin: 10px 6px -10px;
  padding: 12px;
}

.commentsBox h2 {
  margin: 0 0 16px;
  font-size: 20px;
}

.commentsBox form {
  box-shadow: none;
  padding: 0;
  max-width: initial;
  margin: 0;
}

.comment {
  display: flex;
  margin: 16px 0;
}

.commentHeader {
  display: flex;
  align-items: center;
  margin-bottom: 4px;
}

.commentImg {
  border-radius: 50%;
  width: 46px;
  height: 46px;
  overflow: hidden;
  margin-right: 8px;
}

.commentImg img {
  width: 100%;
}

.commentName {
  display: block;
  margin-right: 10px;
  font-weight: 700;
}

.commentDate {
  color: #919191;
}

.projectNav {
  display: flex;
  justify-content: flex-end;
  gap: 14px;
  padding: 12px 18px;
  background: #eaf5ff;
  border-bottom: 1px solid #cccccc;
  border-radius: 8px 8px 0 0;
}

.projectNav button {
  padding: 9px;
  font-size: 15px;
  transition: none;
}

.projectNav button:last-of-type {
  background-color: #fff;
  color: var(--accent-color);
  border: 1px solid var(--accent-color);
}

.projectNav[data-overdue="true"] {
  background-color: rgb(255, 236, 236);
}

.projectNav[data-overdue="true"] button:first-of-type {    
  background-color: red;
}

.projectNav[data-overdue="true"] button:last-of-type {    
  color: red;
  border-color: red;
}

.projectNav[data-finished="true"] {
  background-color: rgb(236, 255, 241);
}

.projectNav[data-finished="true"] button:first-of-type {    
  background-color: #35e454;
}

.projectNav[data-finished="true"] button:last-of-type {    
  color: #35e454;
  border-color: #35e454;
}