.wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 18px;
  padding: 2rem;
}

.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.typeHeading {
  font-size: 20px;
  font-weight: 700;
  color: var(--primary-color);
}

.filterButtons {
  display: flex;
  gap: 8px;
}

.filterBtn {
  cursor: pointer;
  background-color: #fff;
  padding: 10px;
  font-size: 15px;
  color: var(--accent-color);
  border-radius: 5px;
  border: 1px solid var(--accent-color);
  outline: none;
  transition: all .2s;
  white-space: nowrap;
}

.filterBtn:hover {
  color: #fff;
  background-color: var(--accent-color);
}

.filterBtn[data-active="true"] {
  color: #fff;
  background-color: var(--accent-color);
}