.sidebar {
  background-color: #ffffff;
  padding: 2rem;
  width: 250px;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
}

.header {
  color: var(--primary-color);
  margin-bottom: 1.5rem;
  font-size: 1.3rem;
  margin-top: 0;
}

.user {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.statusDot {
  flex-shrink: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: red; /* Standardmäßig auf "offline" (rot) gesetzt */
}

.statusDot[data-active="true"] {
  flex-shrink: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #35e454; /* Standardmäßig auf "offline" (rot) gesetzt */
}

.userImg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.username {
  font-size: 1rem;
  color: #333;
  max-width: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
}