.App {
  min-height: 100vh;
}

.App * {
  box-sizing: border-box;
}

.dashboard {
  display: flex;
  justify-content: space-between;
  min-height: 100vh;
}

.userIconPlaceholder,
.addAttrBtn {
  width: 49px;
  height: 49px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.userIconPlaceholder {
  pointer-events: none;
  border: 1px solid #bbbbbb;
}

.userIconPlaceholder {
  pointer-events: all;
  cursor: pointer;
  border-style: dashed;
  transition: background-color .2s;
}
.userIconPlaceholder:hover {
  background-color: #ebebeb;
}

.userIconPlaceholder svg {
  padding: 12px;
}

.addAttrBtn {
  cursor: pointer;
  width: 44px;
  height: 44px;
  background-color: #eee;
  font-size: 1.5rem;
  border-radius: 5px;
  transition: background-color .2s;
}

.addAttrBtn:hover {
  background-color: #d6d6d6;
}

[class*="projects-grid"] .userIconPlaceholder,
[class*="project-detail"] .userIconPlaceholder,
[class*="project-detail"] .addAttrBtn {
  width: 36px;
  height: 36px;
}
[class*="projects-grid"] .userIconPlaceholder svg,
[class*="project-detail"] .userIconPlaceholder svg {
  padding: 8px;
}
[class*="projects-grid"] .userIconPlaceholder {
  pointer-events: none;
}