.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  width: 100%;
  text-transform: uppercase;
}

.logo {
  color: var(--primary-color);
  font-size: 1.5rem;
  cursor: pointer;
  font-weight: 800;
  text-decoration: none;
}

.links {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.link {
  color: var(--primary-color);
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition: background-color 0.3s ease, color 0.3s ease;
  cursor: pointer;
}

.link:hover {
  background-color: #3498db;
  color: #ffffff;
}